.animated_note::before {
  content: "";
  width: 100%;
  height: 100%;
  /* box-shadow: 0 0 0px 0px #E19E13; */
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 14px;
  animation: blowing_note 3s infinite alternate;
  z-index: -1;
}
.animated_note::after {
  content: "";
  width: 100%;
  height: 100%;
  /* box-shadow: 0 0 0px 0px #E19E13; */
  opacity: 0;
  position: absolute;
  top: 0px;
  left: 0;
  border-radius: 14px;
  animation: blowing_after_note 5s infinite alternate;
  z-index: -1;
}
.animated::before {
  content: "";
  width: 100%;
  height: 100%;
  /* box-shadow: 0 0 0px 0px #E19E13; */
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 14px;
  animation: blowing 3s infinite alternate;
  z-index: -1;
}
.animated::after {
  content: "";
  width: 100%;
  height: 100%;
  /* box-shadow: 0 0 0px 0px #E19E13; */
  opacity: 0;
  position: absolute;
  top: 0px;
  left: 0;
  border-radius:14px;
  animation: blowing_after 5s infinite alternate;
  z-index: -1;
}
/* .animated{
