@keyframes blowing {
  0% {
    box-shadow: 10px -10px 25px -7px #e58c25;
    opacity: 0.7;
  }
  100% {
    box-shadow: 6px -6px 30px -7px #dd5852;
    opacity: 0.9;
  }
}
@keyframes blowing_after {
  0% {
    box-shadow: -4px 6px 30px -10px #00c2ff;
    opacity: 0.6;
  }
  100% {
    box-shadow: -6px 10px 35px -10px #7854bf;
    opacity: 0.8;
  }
}
@keyframes blowing_note {
  0% {
    box-shadow: 10px -10px 25px -7px #00AFA0;
    opacity: 0.7;
  }
  100% {
    box-shadow: 6px -6px 30px -7px #00D75F;
    opacity: 0.9;
  }
}
@keyframes blowing_after_note {
  0% {
    box-shadow: -4px 6px 30px -10px #0D6EF7;
    opacity: 0.6;
  }
  100% {
    box-shadow: -6px 10px 35px -10px #7E54BA;
    opacity: 0.8;
  }
}
